import controller from "../controllers/priceCourse.controller";
import { Router } from "express";
import auth from "../middleware/auth";
const router = Router();

router.put("/form5", controller.updateForm5);

router.get("/", controller.findAll);
router.get("/form5/:courseId", controller.findAllForm5);
router.get("/:id", controller.findOne);
router.post("/", controller.create);
router.post("/form5", controller.createForm5);

router.put("/:id", controller.update);
router.delete("/:id", controller.remove);
router.delete("/form5/:courseId", controller.deleteForm5);

export default router;
